<template>
    <div class='promotion'>
        <div class='top'>
            <div class='parTop'>
                <span @click='goback()' class='back'>运营推广</span>
                <img src='../../assets/images/icon_nav_arrow.png'/>
                <span class='branch free' @click='goback()'>免费体验券</span>
                <img src='../../assets/images/icon_nav_arrow.png'/>
                <span class='branch'>领用详情</span>
            </div>
        </div>
        <Table  :tableData="tableData" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata"></Table>
    </div>
</template>
<script>
import Table from '../../components/Table'
import {cardDetail, updateFreeStatue, freeDetail} from '@/services/service'
import {isApiResponseOk, getSessionStorage, setSessionStorage} from '@/utils/com'
export default {
    data () {
        return {
            tableKey: [
                {name: '序号', value: 'sequence', nor: true},
                {name: '领用人', value: 'personName', nor: true},
                {name: '领取时间', value: 'getTime', nor: true},
                {name: '已使用张数', value: 'couponCount', cardUser: true},
                {name: '最后一次使用时间', value: 'lastUseTime', nor: true}
            ],
            tableData: [],
            isPage: true,
            total: 0,
            tabhide: true,
            pageSize: 10,
            para: {pageNo: 1, pageSize: 10},
            show: true // 控制优惠券显示隐藏的
        }
    },
    methods: {
        freeList () {
            this.$router.push({name: 'promotion', query:{showList: true}})
        },
        getRowdata (data) {
            console.log('数据', data)
            if (data.index === '启用' || data.index === '禁用') {
                // 启用禁用
                this.updateStatus(data.row)
            } else if (data.index === '详情') {
                this.freeDetail(data.row)
            }
        },
        changeParam (data) {
            this.para.pageNo = data
            this.initList()
        },
        async updateStatus (data) {
            var status = ''
            if (parseInt(data.status) === 1) {
                status = 0
            } else if (parseInt(data.status) === 0) {
                status = 1
            }
            const res = await updateFreeStatue(data.code, status)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '修改成功',
                    type: 'success'
                })
                this.initList()
            }
        },
        async freeDetail (data) {
            const res = await freeDetail(data.code)
            if (isApiResponseOk(res)) {
                setSessionStorage('freeDetail', res.data)
                this.$router.push({name: 'addpromotion', query: {modify: true}})
            }
        },
        // 添加活动
        addPlan () {
            this.$router.push({name: 'addpromotion'})
        },
        // 获取优惠券列表
        async initList (data) {
            const res = await cardDetail (data)
            if (isApiResponseOk(res)) {
                this.tableData = res.data.record
                this.total = res.data.totalNum
                console.log('shu ', this.tableData)
            }
        },
        goback () {
            this.$router.go(-1)
        }
    },
    created () {
        let code = this.$route.query.code
        Object.assign(this.para, {code})
        this.initList(this.para)
    },
    watch: {
        $route(to,from){
            this.show = (!to.query.showList) ? true : false
            if (!this.show) {
                this.initList()
            }
        }
    },
    components: {
        Table
    }
}
</script>
<style lang="scss" scoped>
.promotion{
  .top{
    font-family: PingFangSC-Semibold;
    font-size: .21rem;
    color: #263238;
    letter-spacing: 0;
    line-height: .21rem;
    margin-bottom: .3rem;
    display: flex;
    .parTop{
        font-family: PingFangSC-Semibold;
        font-size: .17rem;
        color: #8597A1;
        letter-spacing: 0;
        line-height: .17rem;
        flex: 1;
        .branch{
            font-size: .21rem;
            color: #263238;
            letter-spacing: 0;
            line-height: .21rem;
        }
        img{
            margin-left: .12rem;
            margin-right: .2rem;
        }
        .back{
            cursor: pointer;
        }
        .free{
          font-size: .17rem;
          color: #8597A1;  
        }
    }
    .text{
        font-family: PingFangSC-Medium;
        font-size: .11rem;
        color: #8597A1;
        letter-spacing: 0;
        line-height: .11rem;
        align-items: center;
        display: flex;
        div{
            border: 1px solid #20C6BA;
            border-radius: 2px;
            color: #20C6BA;
            width: 1.29rem;
            height: .36rem;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: .3rem;
            cursor: pointer;
        }
        .icon-btn_icon_add:before{
            margin-right: .1rem;
        }
        .icon-tishi:before{
            font-size: 12px;
            margin-right: 9px;
        }
    }
  } 
  .free{
    width: 2.66rem;
    cursor: pointer;
    img{
        vertical-align: middle;
    }
    .text{
        background: #fff;
        padding: .21rem .24rem;
        .fir{
            font-family: PingFangSC-Medium;
            font-size: .16rem;
            color: rgba(0,0,0,0.85);
            line-height: .24rem;
        }
        .sec{
            font-family: PingFangSC-Regular;
            font-size: .14rem;
            color: rgba(0,0,0,0.45);
            line-height: .22rem;
            margin-top: .04rem;
            margin-bottom: .11rem;
        }
        .thi{
            font-family: HelveticaNeue;
            font-size: .12rem;
            color: rgba(0,0,0,0.45);
            line-height: .2rem;
        }
    }
  }
}
</style>





